<template>
  <div>
    <div class="mt-2">
      <b-row>
        <!-- <b-col cols="4"></b-col> -->
        <b-col cols="12" class="set_c" v-if="item_record != ''">
          <b-table
            :items="item_record"
            :fields="field_record"
            striped
            bordered
            small
            head-variant="dark"
          >
          <template #cell(index)="data">
              {{ data.value }}
            </template>
          </b-table>
        </b-col>
        <!-- <b-col cols="4"></b-col> -->
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      field_record: [
        { key: "index", label: "#" },
        { key: "date_cal", label: "เดือนที่ใช้จ่าย" },
        { key: "total", label: "จำนวน", class: "text-right" },
      ],
      item_record: [],
      date_show:"",
      date_show2:"",
    };
  },
  methods: {
    load_db_record() {
      var self = this;
      var array = [];
      var sum = 0;
      var date = "";
      array = self.$store.getters.load_db;
      array.forEach((element) => {
        sum += Number(element.value);
        date = element.date1;
      });
      var count=0;
      count++;
      var arr_show1= {
        index: count,
        date_cal: (date==""?self.date_show2:date),
        total: sum-5000,
      };
      count++;
      var arr_show2 = {
        index: count,
        date_cal: (date==""?self.date_show:date),
        total: sum,
      };
    //   console.log(arr_show);
      self.item_record = [arr_show1,arr_show2];
    },
    set_date() {
      var self = this;
      const now = new Date();
      const yy = now.getFullYear();
      const mm = now.getMonth() + 1;
      const mm2 = now.getMonth();
      const dd = now.getDate();
      const today =
        yy +
        "-" +
        (mm < 10 ? 0 + "" + mm : mm) +
        "-" +
        (dd < 10 ? 0 + "" + dd : dd);
        const today2 =
        yy +
        "-" +
        (mm2 < 10 ? 0 + "" + mm2 : mm2) +
        "-" +
        (dd < 10 ? 0 + "" + dd : dd);
      self.date_show = today;
      self.date_show2 = today2;
      // console.log(today);
    },
  },
  mounted() {
    var self = this;
    self.set_date();
    self.load_db_record();
    
  },
};
</script>
