<template>
  <div>
    <div>
      <b-row>
        <b-col cols="6"><calculate></calculate></b-col>
        <b-col cols="6">
          <b-row>
            <b-col cols="6" class="set_c">
              <b-form-datepicker
                v-model="value_date"
                :date-format-options="{
                  year: 'numeric',
                  month: 'numeric',
                  day: 'numeric',
                }"
                size="sm"
                locale="th"
              ></b-form-datepicker>
            </b-col>
            <b-col cols="6" class="set_l f_s_80">
              <b-button size="sm" variant="primary" v-on:click="new_load_db"
                >ค้นหา</b-button
              >
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" class="shadow" v-if="item_record2 != ''">
              <b-table
                :items="item_record2"
                :fields="field_record2"
                striped
                bordered
                sticky-header="450px"
                head-variant="dark"
                caption-top
              >
                <template #table-caption
                  ><b-icon-list-stars />&nbsp;ผลรวมค่าใช้จ่ายแต่ละประเภท</template
                >
                <template #cell(index)="data">
                  {{ data.index + 1 }}
                </template>
                <template #cell(date1)="data">
                  {{ data.value == "" ? value_month : data.value }}
                </template>
              </b-table>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import calculate from "@/views/calculate";
export default {
  data() {
    return {
      item_record2: [],
      field_record2: [
        { key: "index", label: "#" },
        { key: "key", label: "ประเภท", class: "text-left" },
        { key: "date1", label: "เดือน" },
        { key: "value", label: "จำนวน", class: "text-right" },
      ],
      value_month: "",
      value_date: "",
    };
  },
  components: {
    calculate,
  },
  methods: {
    load_db_record2() {
      var self = this;
      self.item_record2 = self.$store.getters.load_db;
    },
    new_load_db() {
      var self = this;
      self.load_db_record2();
    },
    get_time() {
      var self = this;
      var mm;
      var date = new Date();
      var mm = date.getMonth() + 1;
      mm = mm < 10 ? "0" + mm : mm;
      var dd = date.getDate();
      dd = dd < 10 ? "0" + dd : dd;
      self.value_month = mm + "/" + date.getFullYear();
      self.value_date = date.getFullYear() + "-" + mm + "-" + dd;
      self.load_db_record2();
      //console.log(self.value_month);
    },
  },
  mounted() {
    var self = this;
    self.get_time();
    //self.load_db_record();
    //self.load_db_record2();
  },
};
</script>
