<template>
  <div>
    <div>
      <b-row>
        <b-col cols="1"></b-col>
        <b-col cols="10" class="shadow" v-if="field_record != ''">
          <b-table
            :items="item_record"
            :fields="field_record"
            striped
            bordered
            sticky-header="500px"
            head-variant="light"
            caption-top
          >
            <template #table-caption
              ><b-icon-list-stars />&nbsp;รายการค่าใช้จ่าย</template
            >
            <template #cell(index)="data">
              {{ data.index + 1 }}
            </template>
            <template #cell(user_id)="data">
              L{{ run_id(data.index + 1) }}
            </template>
            <template #cell(value)="data">
              {{ run_num(data.value) }}
            </template>
            <template #cell(date1)="data">
              {{ (data.value==''?date_now:data.value) }}
            </template>
            <template #cell(date2)="data">
              {{ (data.value==''?date_now:data.value) }}
            </template>
          </b-table>
        </b-col>
        <b-col cols="1"></b-col>
      </b-row>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      field_record: [
        { key: "index", label: "#" },
        { key: "user_id", label: "ID" },
        { key: "key", label: "Category", class: "text-left" },
        { key: "value", label: "Money", class: "text-right" },
        { key: "date1", label: "Date Calulate" },
        { key: "date2", label: "Date Save" },
      ],
      item_record: [],
      date_now:"",
    };
  },
  methods: {
    load_db_record() {
      var self = this;
      self.item_record = self.$store.getters.load_db;
    },
    run_id(val) {
      let text = "";
      text = String("00000" + val).slice(-5);
      return text;
    },
    run_num(val) {
      let text = "";
      text = Number(val).toFixed(2);
      return text;
    },
    set_date() {
      var self = this;
      const now = new Date();
      const yy = now.getFullYear();
      const mm = now.getMonth() + 1;
      const dd = now.getDate();
      const today =
        yy +
        "-" +
        (mm < 10 ? 0 + "" + mm : mm) +
        "-" +
        (dd < 10 ? 0 + "" + dd : dd);
      self.date_now = self.date_cal = today;
    },
  },
  mounted() {
    var self = this;
    self.load_db_record();
    self.set_date();
  },
};
</script>
