<template>
  <div>
    <div class="mt-3">
      <b-row>
        <b-col cols="4" class="set_r mt-2">หัวข้อ&nbsp;:</b-col>
        <b-col cols="4" class="set_l mt-1" v-if="option_cetagory != ''">
          <b-form-select
            v-model="select_cetagory"
            :options="option_cetagory"
            size="sm"
          ></b-form-select>
        </b-col>
        <b-col cols="4" class="set_l"></b-col>
      </b-row>
      <b-row>
        <b-col cols="4" class="set_r mt-2">จำนวน&nbsp;:</b-col>
        <b-col cols="4" class="set_l mt-1">
          <b-form-input
            type="number"
            size="sm"
            v-model="use_money"
          ></b-form-input>
        </b-col>
        <b-col cols="4" class="set_l"></b-col>
      </b-row>
      <b-row>
        <b-col cols="4" class="set_r mt-2">วันที่ใช้คำนวณ&nbsp;:</b-col>
        <b-col cols="4" class="set_l mt-1">
          <b-form-datepicker
            v-model="date_cal"
            :date-format-options="{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
            }"
            size="sm"
            locale="th"
          ></b-form-datepicker>
        </b-col>
        <b-col cols="4" class="set_l"></b-col>
      </b-row>
      <b-row>
        <b-col cols="4" class="set_r mt-2">วันที่บันทึก&nbsp;:</b-col>
        <b-col cols="4" class="set_l mt-1">
          <b-form-datepicker
            v-model="date_save"
            :date-format-options="{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
            }"
            size="sm"
            disabled
          ></b-form-datepicker>
        </b-col>
        <b-col cols="4" class="set_l"></b-col>
      </b-row>
      <b-row>
        <b-col cols="4"></b-col>
        <b-col cols="4 mt-1"
          ><b-button
            size="sm"
            variant="success"
            block
            v-if="select_cetagory != '' && use_money != ''"
            v-on:click="inset_db_money"
            ><b-icon icon="file-arrow-down-fill"></b-icon>&nbsp;Save</b-button
          ><b-button size="sm" variant="success" block disabled v-else
            ><b-icon icon="file-arrow-down-fill"></b-icon>&nbsp;Save</b-button
          ></b-col
        ><b-col cols="4"></b-col>
      </b-row>
    </div>
    <div class="mt-2">
      <b-alert show variant="success" v-show="show_save != ''">{{
        show_save
      }}</b-alert>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      select_cetagory: "",
      option_cetagory: [],
      use_money: 0,
      date_cal: "",
      date_save: "",
      show_save: "",
    };
  },
  methods: {
    set_date() {
      var self = this;
      const now = new Date();
      const yy = now.getFullYear();
      const mm = now.getMonth() + 1;
      const dd = now.getDate();
      const today =
        yy +
        "-" +
        (mm < 10 ? 0 + "" + mm : mm) +
        "-" +
        (dd < 10 ? 0 + "" + dd : dd);
      self.date_save = self.date_cal = today;
      // console.log(today);
    },
    load_db_cetagory() {
      var self = this;
      self.option_cetagory = self.$store.getters.load_cetagory;
    },
    inset_db_money() {
      var self = this;
      var arr = {
        key: self.select_cetagory,
        value: self.use_money,
        date1: self.date_cal,
        date2: self.date_save,
      };
      self.$store.dispatch("act_increment", arr);
      self.show_save ="รายการ "+self.$store.getters.get_count+
        " , เพิ่ม : " +
        self.select_cetagory +
        " , จำนวน : " +
        self.use_money +
        " เรียบร้อย!";
      
    },
  },
  mounted() {
    var self = this;
    self.load_db_cetagory();
    self.set_date();
  },
};
</script>
