import Vue from 'vue'
import Router from 'vue-router'
import home from './views/home.vue'
import record from './views/record.vue'
import report from './views/report.vue'
import calculate from './views/calculate.vue'
import summary_month from './views/summary_month.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: { title: 'Home' }
    },
    {
      path: '/record',
      name: 'record',
      component: record,
      meta: { title: 'Record' }
    },
    {
      path: '/report',
      name: 'report',
      component: report,
      meta: { title: 'Report' }
    },
    {
      path: '/calculate',
      name: 'calculate',
      component: calculate,
      meta: { title: 'Calculate' }
    },
    {
      path: '/summary_month',
      name: 'summary_month',
      component: summary_month,
      meta: { title: 'Summary Month' }
    }
       
  ]
});
router.afterEach((to, from) => {
  Vue.nextTick(() => {
      document.title = to.meta.title;
  });
});
export default router;
